    transition: opacity .5s ease .5s, transform .5s ease .5s; }
  .slider figure figcaption .more-link {
    transition: opacity .5s ease .8s, transform .5s ease .8s; }
  .slider figure.active-slide h1, .slider figure.active-slide h2, .slider figure.active-slide .more-link {
    opacity: 1;
    transform: translate(0, 0); }

.grid figure {
  position: relative; }
  .grid figure:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0.2);
    transition: background .3s ease; }
  .grid figure:hover:before {
    background: rgba(0, 0, 0, 0.5); }
  .grid figure figcaption h1 {
    font-size: 1.5rem; }
  .grid figure figcaption h2 {
    display: none; }

section[rel="Über uns"] {
  text-align: center; }
  section[rel="Über uns"] i {
    font-size: 5rem; }

.slider figure, .single figure {
  height: 600px; }

