.grid {

  figure {
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(0, 0, 0, 0.2);
      transition: background .3s ease;
    }
    &:hover:before {
      background: rgba(0, 0, 0, 0.5);
    }
    figcaption {
      h1 {
        font-size: 1.5rem;
      }

      h2 {
        display: none;
      }
    }
  }
}

section[rel="Über uns"] {
  text-align: center;
  i {
    font-size: 5rem;

  }
}

.slider, .single {
  figure {
    height: 600px;
  }
}

